import TicketListItem from './TicketListItem';
    export default {
        data () {
            return {
                ticketList: [
                    // {
                    //     ID: '1423',
                    //     Subject: 'my computer cannot work',
                    //     Status: 'In Process',
                    //     Channel: 'WeChat'
                    // },
                    // {
                    //     ID: '1421',
                    //     Subject: 'my computer cannot work',
                    //     Status: 'In Process',
                    //     Channel: 'WeChat'
                    // },
                    // {
                    //     ID: '1422',
                    //     Subject: 'my computer cannot work',
                    //     Status: 'In Process',
                    //     Channel: 'WeChat'
                    // },
                    // {
                    //     ID: '1419',
                    //     Subject: 'my computer cannot work',
                    //     Status: 'In Process',
                    //     Channel: 'WeChat'
                    // }
                ]
            }
        },
        components: {
            TicketListItem: TicketListItem
        },
        methods: {
            openTicketDetail: function (id) {
                console.log(id);
                this.$router.push('/TicketDetail/' + id);
            }
        },
        mounted() {
            let loading = this.$loading();
            this.$axios({
                method: 'post',
                url: this.CONFIG.url.getTicketList
            }).then((res) => {
                loading.close();
                this.ticketList = res.data;
            })
        },
    }



